import axios from '@/utils/uniAxios.js';
import {  
    mapMutations,mapState
} from 'vuex';
export default {
	data() {
		return {
			person:{
				portrait:'',
				userName:'',
				gender:'',
				birthday:'',
				weight:'',
			},
			id:'',
			gender:''
		};
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	onLoad(option){
		this.id = option.id;
		this.getData();
		
	},
	methods:{
		...mapMutations(['logout']),
		// 数据初始化
		getData(){
			let obj ={
				id:this.id
			}
			// 优先判断是不是微信登录
			if(!uni.getStorageSync('isCanUse')){
				this.person = this.$store.state.userInfo;
				console.log(this.person )
				if(this.person.gender==1){
					this.gender = '男'
				}else if(this.person.gender==0){
					this.gender = '女'
				}else{
					this.gender=''
				};
				return;
			}else if(uni.getStorageSync('setPhone')){//判断是不是手机登录
				axios.post('/sso/user/id',obj).then(res=>{
					// this.person = res.data.data;
					this.person = {
						avartar:'',
						userName:'',
						sex:'',
						birthday:'',
						weight:'',
					}
				})
			}
		},
		navTo(url){
		},
		//保存
		save(){
			// 返回上一页
			uni.navigateBack()
		},
	}
}
